<template>
  <transition name='fade'>
    <div class='publishInfoPage'>
      <v-Header class="head">
        <span slot='text'>我发布的{{title}}</span>
      </v-Header>
      <div id='listsScrollCon' class='listsScrollCon'  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <v-rendezvousList :Lists='Lists' />
        <div class='tips' v-if='Lists1.length>0'><span>———    以下内容已下架    ———</span></div>
        <v-rendezvousList :Lists='Lists1' />
      </div>
      <div class='publishBtn' @click='publish'>发布{{title}}</div>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header';
  import rendezvousList from '../rendezvous/rendezvousList'
  import { mapState } from 'vuex';
  export default {
      name: '',
      components: {'v-rendezvousList':rendezvousList,'v-Header':Header},
      props: [],
      data () {
        return {
            Lists:[],
            Lists1:[],
            type:this.$route.query.publishInfoType,
            lastProductId:0,
            loading:false,
            loadType:'more'
        }
      },
      watch: {},
      computed: {
        ...mapState({
          _userType:state => state.userInfo.userType,//当前用户id
        }),
        title(){
          return `${this.type==3?'行程':this.type==5?'课程':'装备'}`;
        }
      },
      created () {
        this.getList();
         this.$http.get(this.$port.MY_CONT_PRODUCT,{
            params:{
              contType:this.type,
              contStatus:2,
              lastProductId:0
            }
          }).then((response) => {
            const {data} = response;
            if(response.code == 200){
              this.Lists=this.Lists.concat(data);
            }
          })
          .catch((error) => {
            console.log(error.response.data);
          });
      },
      methods: {
        publish(){
          if(this._userType==2||this._userType==3||this._userType==4){
            this.$router.push({path:'/write/cover',query:{contType:this.type}});
          }else{
            alert('暂无发布文章权限，请到个人主页进行身份验证吧^_^');
          }
        },
        loadMore(){
          console.log(1)
          this.getList();
        },
        getList() {
          if(this.loadType == 'all'||this.loading) return;
          this.loading = true;
          this.$http.get(this.$port.MY_CONT_PRODUCT,{
            params:{
              contType:this.type,
              contStatus:3,
              lastProductId:this.lastProductId
            }
          }).then((response) => {
            const {data} = response;
            this.loading = false;
            if(response.code == 200){
              this.Lists1=this.Lists1.concat(data);
              if(data.length>0){
                this.loadType = data.length<10?'all':'more';
                this.lastProductId = data.pop().productId;
              }else{
                this.loadType = 'all';
              }
            }
          })
          .catch((error) => {
            this.loading = false;
            console.log(error.response.data);
          });
        },
      },
    }
</script>
<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.publishInfoPage{
  width:100%;
  height:100%;
  position: absolute;
  top:0;
  left:0;
  overflow: hidden;
  background:white;
  .listsScrollCon{
    position: absolute;
    top:0.82rem;
    left:0;
    right:0;
    bottom:0.88rem;
    margin:auto;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling:touch;
    .tips{
      width:100%;
      height:1.5rem;
      font-size:0.28rem;
      line-height:1.5rem;
      text-align:center;
      color:#e5e5e5;
      // margin-bottom:-0.3rem;
      border-bottom: 1px solid #e5e5e5;
    }
  }
  .publishBtn{
      width:100%;
      height:0.88rem;
      position:fixed;
      bottom:0;
      left:0;
      background:url('../../assets/images/publishBtn.jpg') no-repeat;
      background-size:cover;
      color:white;
      line-height:0.88rem;
      font-size:0.36rem;
      text-align:center;
    }
}
</style>


